import cartActiontypes, { CartToggleActionTypes } from '../cart/cart.actiontypes';
const INTITIAL_STATE = {
    hidden: true
}


const toggleCart = (state = INTITIAL_STATE, action) => {
    switch (action.type) {
        case cartActiontypes.CART_TOGGLE:
            return {
                ...state,
                hidden: !state.hidden
            }
        default:
            return state;
    }

}
export default toggleCart;